*{
    margin: 0;
    padding: 0;
}

.contenedor-inicio{
    width: 100%;
    height: 100vh;
}

.imagen-inicio{
    width: 100%;
    height: 100vh;
    object-fit: cover;
}

.contenedor-servicio{
    width: 50vw;
    background-color: #354F52;
    color: #fff;
    position: relative;
    padding-top: 15px;
    padding-bottom: 15px;
    bottom: 600px;
    box-shadow: 1px 1px 8px #000;
    border-bottom-color: 800px;
    opacity: 0.7;
}

.texto-servicio{
    font-size: 3vw;
    font-weight:bold;
    font-family: Lato, sans-serif;
    text-align: center;
    margin-bottom: 20px;
}

.texto-servicio:hover{
    color: #CAD2C5;
    transition: 1s;
}

.texto-caracteristicas{
    font-size:2.5vw;
    font-family: Roboto;
    color: #eef614;
    text-align: center;
}

.texto-caracteristicas:hover{
    font-size:3vw;
    transition: 0.5s;
}

