    display: flex;
    margin-bottom: 10px;
}

.logo-bara{
    display: flex;
    flex-wrap: wrap;

    height: 2vh;
    margin-top: 10px;
    margin-left: 3px;
    background-color: rgb(111, 111, 147);
}

