*{
    margin: 0;
    padding: 0;
}


.servicios-contenedor{
    width: 100%;
    height: 340px;
    position: relative;
    bottom: 150px;
    display: flex;
    text-align: center;
    justify-content: center;
}

.card{
    width: 200px;
    height: 300px;
}

.imagen-card{
    width: 170px;
    border-radius: 20px;
}

.imagen-card:hover{
    box-shadow: 1px 1px 20px rgb(77, 255, 22);
    width: 120px;
    transition: 0.5s;
}

.titulo-card{
    font-family: Roboto, sans-serif;
    color: #fff;
    font-size: 20px;
}

.texto-card{
    font-family:'Arial Narrow';
    font-weight: 200;
    color: #fff;
    margin: 7px 0px;
}

